<template>
  <div>
    <div class="card" ref="eventCard">
      <div class="card-body">
        <div class="card-title">
          <h3><font-awesome-icon :icon="['fas', 'database']" /> 公开数据表</h3>
          <hr />
        </div>
        <vs-table ref="table" v-model="selected">
          <template #thead>
            <vs-tr>
              <vs-th>
                <vs-checkbox :indeterminate="selected.length == events.length" v-model="allCheck" @change="selected = $vs.checkAll(selected, events)" />
              </vs-th>
              <vs-th> 事件名 </vs-th>
              <vs-th> 事件ID </vs-th>
            </vs-tr>
          </template>
          <template #tbody>
            <vs-tr :key="i" v-for="(tr, i) in $vs.getPage(events, page, max)" :data="tr" :is-selected="!!selected.includes(tr)">
              <vs-td style="width: 5%" checkbox>
                <vs-checkbox :val="tr" v-model="selected" />
              </vs-td>
              <vs-td>
                {{ tr.eventName }}
              </vs-td>
              <vs-td>
                {{ tr.eventID }}
              </vs-td>
              <template #expand>
                <div class="con-content">
                  <div class="d-flex align-items-center">
                    <vs-button warn flat @click="eventDatas = parseJsonData(tr.jsonData)">修改数据</vs-button>
                  </div>
                </div>
              </template>
            </vs-tr>
          </template>
          <template #footer>
            <vs-pagination v-model="page" :length="$vs.getLength(events, max)" />
          </template>
        </vs-table>
        <div class="mt-3 card-title">
          <h3><font-awesome-icon :icon="['fas', 'wrench']" /> 修改数据表</h3>
          <hr />
        </div>
        <vs-table ref="table" v-model="selectedEventData">
          <template #thead>
            <vs-tr>
              <vs-th>
                <vs-checkbox :indeterminate="selectedEventData.length == eventDatas.length" v-model="allCheckEventData" @change="selectedEventData = $vs.checkAll(selectedEventData, eventDatas)" />
              </vs-th>
              <vs-th> 索引 </vs-th>
              <vs-th> 事件数据 </vs-th>
            </vs-tr>
          </template>
          <template #tbody>
            <vs-tr :key="i" v-for="(tr, i) in eventDatas" :data="tr" :is-selected="!!selectedEventData.includes(tr)">
              <vs-td style="width: 5%" checkbox>
                <vs-checkbox :val="tr" v-model="selectedEventData" />
              </vs-td>
              <vs-td>
                {{ i }}
              </vs-td>
              <vs-td>
                {{ tr.toString() }}
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJS from "crypto-js/crypto-js";
export default {
  name: "Manage",
  data() {
    return {
      page: 1,
      max: 10,
      allCheck: false,
      allCheckEventData: false,
      events: [],
      eventDatas: [],
      selected: [],
      selectedEventData: [],
    };
  },
  mounted() {
    this.refreshData();
  },
  methods: {
    async refreshData() {
      const loading = this.$vs.loading({
        target: this.$refs.eventCard,
      });
      await this.$store
        .dispatch("utils/request", {
          method: "post",
          url: "/a/getPublicEventList",
        })
        .then((res) => {
          if (res.data.status !== 1) {
            this.$toastx({
              type: "danger",
              title: "获取数据失败",
            });
            return;
          }
          this.events = res.data.data;
        });

      loading.close();
    },
    parseJsonData(data) {
      if (!data) {
        this.$swal("无数据");
        return {};
      }
      var words = CryptoJS.enc.Base64.parse(data);
      return JSON.parse(CryptoJS.enc.Utf8.stringify(words)).data;
    },
  },
};
</script>

